.sub-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  padding: 0px 30px;
  height: 66px;
  width: calc(100% - 356px);
  margin: 0 auto;
}
.nav-container-sub {
  position: sticky;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 120;
}
.nav-link {
  font-size: 14px; /* Set your desired font size here */
  text-decoration: none; /* Remove the default underline for all links */
  color: black; /* Set the default color for links */
}

.nav-link.active {
  color: blueviolet; /* Add underline style for active links */
}
.browse-section {
  display: flex;
  color: #646a7b;
  font-size: 14px;
  width: 90px;
  justify-content: space-between;
  align-items: center;
}
.sub-nav-list {
  width: 100%;
  justify-content: flex-end;
  padding: 0 10px;
  margin: 0;
}
.sub-nav-first-child {
  margin-right: auto;
  margin-left: 0;
}
.font20 {
  font-size: 20px;
  font-weight: bold;
}

.react-icon {
  margin-left: 5px;
}

@media screen and (max-width: 836px) {
  .click-dropdown.active {
    right: -11px !important;
    left: -97vw !important;
  }

  .dropdown-container .subnav-ul li a {
    font-size: 13px;
    font-weight: 400;
    color: #333333;
  }
}
